* { 
  margin: 0;
  padding: 0;
  box-sizing: border-box; 
  font-family: 'Fira Sans', 'sans-serif'; 
}
.App { 
  min-height: 100vh; 
  position:relative; 
  background-color: #dbd9d9;
}
.camera { 
  position: relative; 

}

.result { 
  position:fixed; 
  top: 0; 
  left: 100%; 
  width: 100%;
  height: 100%; 
  display:flex;
  align-items: center;
  transition: 0.2s; 
  background-color: #dbd9d9; 
}

.result.hasPhoto { 
  left:0%;

}

video { 
  width: 100%;
  max-width: 100%; 
  height: auto;  
}



button.Reset { 
  position:absolute;
  top: 6%;
  right: 10%;
  left: auto; 

  appearance: none;  
  padding: 8px 16px; 
  background-image: rgb(243, 245, 247); 
  background-position: 0%; 
  background-size: 200%; 
  color: rgb(2, 6, 10);
  border-style: solid 2px; 
  border-color:rgb(2, 6, 10); 
  border-width: 3px; 
}

canvas { 
  width: 100%; 
  height: 100%;
  position:absolute;  
}


#selectionboxdiv { 
  position:absolute; 
}

#filetag { 
  margin-top:8%; 

  appearance: none; 
  
  padding: 8px 16px; 
  background-image: rgb(243, 245, 247); 
  background-position: 0%; 
  background-size: 200%; 
  color: rgb(2, 6, 10);
  border-style: solid 2px; 
  border-color:rgb(2, 6, 10); 
  border-width: 3px; 

}

button { 
  position: absolute; 
  top: 6%; 
  left: 10%; 

  appearance: none; 
  
  padding: 8px 16px; 
  background-image: rgb(243, 245, 247); 
  background-position: 0%; 
  background-size: 200%; 
  color: rgb(2, 6, 10);
  border-style: solid 2px; 
  border-color:rgb(2, 6, 10); 
  border-width: 3px; 
}


#preview { 
  position:absolute; 
  left:10%; 
  display:none; 
}

#displayimg { 
  position:absolute; 
  left:10%;
  width:300px; 
  height:300px; 
}

.analyzed { 
  display:none; 
}
.analyzed.true{
  position:absolute; 
  left:50%;
  display:block; 
}